import React, { Component } from 'react';
import './App.css';

export default class IdealTeaMateScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      text: (<div> </div>),
      text_plainText: " ",
      textcopy: (<div> </div>),
      textcopy_plainText: " ",
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const value_text = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_class1'] : '');
    
    const style_elText = {
      color: 'black',
      textAlign: 'left',
     };
    
    const value_textCopy = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_interest1'] : '');
    
    const style_elTextCopy = {
      color: 'black',
      textAlign: 'left',
     };
    
    return (
      <div className="AppScreen IdealTeaMateScreen" style={baseStyle}>
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elText">
            <div className="baseFont" style={style_elText}>
              <div>{value_text !== undefined ? value_text : (<span className="propValueMissing">{this.state.text}</span>)}</div>
            </div>
          </div>
          
          <div className="elTextCopy">
            <div className="baseFont" style={style_elTextCopy}>
              <div>{value_textCopy !== undefined ? value_textCopy : (<span className="propValueMissing">{this.state.textcopy}</span>)}</div>
            </div>
          </div>
        </div>
        
      </div>
    )
  }
  
}
