import React, { Component } from 'react';
import './App.css';

import btn_icon_411338 from './images/btn_icon_411338.png';
import btn_icon_904389 from './images/btn_icon_904389.png';
import btn_icon_463794 from './images/btn_icon_463794.png';

// UI framework component imports
import Select from 'muicss/lib/react/select';
import Option from 'muicss/lib/react/option';
import Button from 'muicss/lib/react/button';

export default class AboutMeScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  constructor(props) {
    super(props);
    
    this.state = {
      email: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['userEmail'] : '') ||'',
      password: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_userpassword'] : '') ||'',
      name: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_fullName'] : '') ||'',
      major: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_major'] : '') ||'',
      minor: (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_minor'] : '') ||'',
      classes: '',
      picker: '',
      elTextBlock_visible: false,
      textblock: (<div>Class 1 added! You can change it by reentering a class.</div>),
      textblock_plainText: "Class 1 added! You can change it by reentering a class.",
      textblock2: (<div>Tell us about yourself</div>),
      textblock2_plainText: "Tell us about yourself",
      textblockcopy: (<div>Select an interest:</div>),
      textblockcopy_plainText: "Select an interest:",
    };
  }

  componentDidMount() {
  }

  componentWillUnmount() {
  }

  componentDidUpdate() {
  }

  textInputChanged_elEmail = (event) => {
    this.setState({email: event.target.value});
    
    this.props.appActions.updateDataSlot("userEmail", event.target.value);
  }
  
  textInputChanged_elPassword = (event) => {
    this.setState({password: event.target.value});
    
    this.props.appActions.updateDataSlot("ds_userpassword", event.target.value);
  }
  
  textInputChanged_elName = (event) => {
    this.setState({name: event.target.value});
    
    this.props.appActions.updateDataSlot("ds_fullName", event.target.value);
  }
  
  textInputChanged_elMajor = (event) => {
    this.setState({major: event.target.value});
    
    this.props.appActions.updateDataSlot("ds_major", event.target.value);
  }
  
  textInputChanged_elMinor = (event) => {
    this.setState({minor: event.target.value});
    
    this.props.appActions.updateDataSlot("ds_minor", event.target.value);
  }
  
  textInputChanged_elClasses = (event) => {
    this.setState({classes: event.target.value});
  }
  
  pickerValueChanged_elPicker = (event) => {
    this.setState({picker: event.target.value});
    
    this.props.appActions.updateDataSlot("ds_interest1", event.target.value);
  }
  
  onClick_elAddInterests = (ev) => {
    let newVal = this.state.classes;
    this.props.appActions.updateDataSlot('ds_class1', newVal);
  
    // Perform action 'Clear text' on element 'classes'
    this.setState({
      classes: "",
    });
  
    this.setState({elTextBlock_visible: true});
  
  }
  
  
  onClick_elBack = (ev) => {
    // Go back in screen navigation history
    this.props.appActions.goBack();
  
  }
  
  
  onClick_elContinue = (ev) => {
    // Go to screen 'IdealTeaMate'
    this.props.appActions.goToScreen('idealTeaMate', { transitionId: 'fadeIn' });
  
  }
  
  
  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    
    const style_elRectangle = {
      background: 'rgba(254, 255, 255, 1.000)',
      border: '0.5px solid rgba(0, 0, 0, 0.4500)',
     };
    
    const value_email = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['userEmail'] : '');
    
    const style_elEmail = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const value_password = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_userpassword'] : '');
    
    const style_elPassword = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const value_name = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_fullName'] : '');
    
    const style_elName = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const value_major = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_major'] : '');
    
    const style_elMajor = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const value_minor = (this.props.appActions.dataSlots ? this.props.appActions.dataSlots['ds_minor'] : '');
    
    const style_elMinor = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    const style_elClasses = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      textAlign: 'left',
      pointerEvents: 'auto',
     };
    
    let selection_picker = this.state.picker;
    // Source datasheet and selected data column for picker element 'picker'
    const dataSource_picker = this.props.appActions.getDataSheet('interests');
    const valueColumnName_picker = 'Interests';
    const labelColumnName_picker = 'Interests';
    
    const style_elPicker = {
      pointerEvents: 'auto',
     };
    
    const style_elAddInterests = {
      display: 'block',
      color: '(null)',
      textAlign: 'left',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    const style_elBack = {
      display: 'block',
      textTransform: 'uppercase',
      backgroundImage: 'url('+btn_icon_904389+')',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '88.052%',
      backgroundPosition: '50% 0%',
      color: '(null)',
      textAlign: 'left',
      backgroundColor: 'transparent',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    
    const style_elContinue = {
      display: 'block',
      textTransform: 'uppercase',
      backgroundImage: 'url('+btn_icon_463794+')',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '88.596%',
      backgroundPosition: '50% 0%',
      color: '(null)',
      textAlign: 'left',
      backgroundColor: 'transparent',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elTextBlock = {
      fontSize: 10.3,
      color: 'black',
      textAlign: 'left',
     };
    const elTextBlock = this.state.elTextBlock_visible ? (
      <div className="systemFontRegular  elTextBlock" style={style_elTextBlock}>
        <div>{this.state.textblock}</div>
      </div>
     ) : null;
    const style_elTextBlock2 = {
      color: 'black',
      textAlign: 'left',
     };
    const style_elTextBlockCopy = {
      fontSize: 10.3,
      color: 'black',
      textAlign: 'center',
     };
    
    return (
      <div className="AppScreen AboutMeScreen" style={baseStyle}>
        <div className="background">
          <div className="containerMinHeight elBackground" style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
          
          <div className="elRectangle" style={style_elRectangle} />
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className="elSpacer">
            <div />
          </div>
          
          <div className="elEmail">
            <input className="baseFont" style={style_elEmail} type="email" placeholder="Email Address" onChange={this.textInputChanged_elEmail} value={value_email !== undefined ? value_email : ''}  />
          </div>
          
          <div className="elPassword">
            <input className="baseFont" style={style_elPassword} type="password" placeholder="Password" onChange={this.textInputChanged_elPassword} value={value_password !== undefined ? value_password : ''}  />
          </div>
          
          <div className="elName">
            <input className="baseFont" style={style_elName} type="text" placeholder="Full Name" onChange={this.textInputChanged_elName} value={value_name !== undefined ? value_name : ''}  />
          </div>
          
          <div className="elMajor">
            <input className="baseFont" style={style_elMajor} type="text" placeholder="Major" onChange={this.textInputChanged_elMajor} value={value_major !== undefined ? value_major : ''}  />
          </div>
          
          <div className="elMinor">
            <input className="baseFont" style={style_elMinor} type="text" placeholder="Minor" onChange={this.textInputChanged_elMinor} value={value_minor !== undefined ? value_minor : ''}  />
          </div>
          
          <div className="elClasses">
            <input className="baseFont" style={style_elClasses} type="text" placeholder="Class 1" onChange={this.textInputChanged_elClasses} value={this.state.classes}  />
          </div>
          
          <div className="elPicker">
            <Select className="baseFont" style={style_elPicker}  onChange={this.pickerValueChanged_elPicker} value={selection_picker} >
              {dataSource_picker.items.every(item => {
                return item[valueColumnName_picker] !== selection_picker;
              }) ? <Option value=''/> : null}
              {dataSource_picker.items.map(item => {
                const colValue = item[valueColumnName_picker];
                const labelColValue = item[labelColumnName_picker];
                return <Option key={item.key} value={colValue} label={labelColValue} />
              })}
            </Select>
          </div>
          
          <div className="elAddInterests">
            <Button className="actionFont" style={style_elAddInterests}  variant="fab" color="accent" onClick={this.onClick_elAddInterests} >
              <img src={btn_icon_411338} alt="" style={{width: '100%', marginTop: '4%'}} />
            </Button>
          </div>
          
          <div className="elBack">
            <button className="actionFont" style={style_elBack} onClick={this.onClick_elBack}  />
          </div>
          
          <div className="elContinue">
            <button className="actionFont" style={style_elContinue} onClick={this.onClick_elContinue}  />
          </div>
        </div>
        
        <div className="screenFgContainer">
          <div className="foreground">
            { elTextBlock }
            <div className="baseFont elTextBlock2" style={style_elTextBlock2}>
              <div>{this.state.textblock2}</div>
            </div>
            <div className="systemFontRegular  elTextBlockCopy" style={style_elTextBlockCopy}>
              <div>{this.state.textblockcopy}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  
}
